import React from "react";
import {Button, Col, Container, Form, Row} from "react-bootstrap";
import {designService} from "../../../services";
import {toast} from "react-toastify";
import {trackPromise} from "react-promise-tracker";


export default class AddDesign extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            form: {
                name: '',
                description: '',
                quantity: 1,
                sewingValue: 0,
                type: 1,
                startTime: new Date(),
                isComplete: false
            },
            validated: false,
            isLoading: false
        };

        this.handleInputChange = this.handleInputChange.bind(this);
    }

    notifyError = (error) => toast.error(error);
    notifySuccess = (msg) => toast.success(msg);

    handleSubmit = (event, data) => {

        const form = event.currentTarget;

        if (form.checkValidity() === false) {
            this.setState({validated: true});
        } else {
            this.setState({isLoading: true});
            trackPromise(
            designService.addDesign(this.state.form)
                .then(
                    data => {
                        this.setState({
                            isLoading: false,
                            form: {
                                ...this.state.form,
                                name: ""
                            },
                        });
                        this.notifySuccess(`Design "${data.name}" added successfully`);
                    },
                    error => {
                        this.notifyError(error);
                    }
                ));
        }
        event.preventDefault();
        event.stopPropagation();
    };

    handleDesignTypeChange = (event) => {
        const target = event.target;
        const type = parseInt(target.value);
        const form = {
            ...this.state.form,
            type: type
        }
        this.setState({
            form: form,
        });
    }

    handleInputChange = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.type === 'number' ? (parseInt(target.value) ? parseInt(target.value) : '') : target.value;
        const name = target.name;
        console.log(name);

        this.setState({
            form: {
                ...this.state.form,
                [name]: value
            }
        });
    }

    render() {
        return (

            <Container>
                <br/>
                <h1>Add Design</h1>
                <br/>
                <Form noValidate validated={this.state.validated} onSubmit={this.handleSubmit}>
                    <Form.Group as={Row} id="name">
                        <Form.Label column sm="2">
                            Name
                        </Form.Label>
                        <Col sm="10">
                            <Form.Control type="text" name={'name'} value={this.state.form.name}
                                          onChange={this.handleInputChange} required/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} id="description">
                        <Form.Label column sm="2">
                            Description
                        </Form.Label>
                        <Col sm="10">
                            <Form.Control type="text" name={'description'} value={this.state.form.description}
                                          onChange={this.handleInputChange} required/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} controlId="exampleForm.ControlSelect1">
                        <Form.Label column sm="2">Select Design Type</Form.Label>
                        <Col sm="10">
                            <Form.Control as="select" name={'type'} onChange={this.handleDesignTypeChange}>
                                <option value="1">Sawing</option>
                                <option value="2">Helper</option>
                            </Form.Control>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} id="quantity">
                        <Form.Label column sm="2">
                            Quantity
                        </Form.Label>
                        <Col sm="10">
                            <Form.Control type={'number'} name={'quantity'} value={this.state.form.quantity} onChange={this.handleInputChange}/>
                        </Col>
                    </Form.Group>

                    <Form.Group as={Row} id="sewingValue">
                        <Form.Label column sm="2">
                            Sewing Value
                        </Form.Label>
                        <Col sm="10">
                            <Form.Control type={'number'} name={'sewingValue'} value={this.state.form.sewingValue} onChange={this.handleInputChange}/>
                        </Col>
                    </Form.Group>
                    <Button type="submit" disabled={this.state.isLoading} variant={'dark'}>{this.state.isLoading ? 'Loading' : 'Add Design'}</Button>
                </Form>

            </Container>
        );
    }
}
